import React, { Component } from "react";
import { Grid } from "react-mdl";

class AboutMe extends Component {
  render() {
    return (
      <div style={{ width: "100%" }}>
        <Grid className="landing-body">
          <div className="aboutme-text">
            <h1>About Me</h1>
            <hr />
            <p>My Skillset</p>
          </div>
        </Grid>
      </div>
    );
  }
}

export default AboutMe;
